:root {
  --color-text: #fff;
  --color-title: #000;
  --color-genre: #ff6b08;
  --bgc-btn: #ff6b01;
  --color-underline: #ff001b;
  --bgc-footer-arrow: #f7f7f7;
  --color-footer: #545454;
  --bgc: #fff;
  --color-category: #8c8c8c;
  --fontFamily-roboto: "Roboto", normal;
  --default-transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --backdrop-bg: rgba(0, 0, 0, .2);
  --cubic-bezier: cubic-bezier(.82, .76, .4, .91);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

body.show-modal .backdrop, body.show-modal-team .backdrop-team {
  pointer-events: initial;
  visibility: visible;
  opacity: 1;
}

.header {
  width: 100%;
  height: auto;
  background-image: url("abstract-background(320px).95431ac9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
}

.header__thumb {
  justify-content: space-between;
  align-items: center;
  padding: 40px 0 54px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__thumb {
    padding: 40px 0;
  }
}

.header__nav {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__form {
  justify-content: center;
  padding-bottom: 92px;
  display: flex;
}

.header__container {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header {
    background-image: url("abstract-background(768px).96ec9f06.jpg");
  }
}

@media screen and (min-width: 1200px) {
  .header {
    background-image: url("abstract-background.6bb790b1.jpg");
  }
}

.navigation {
  color: var(--color-text);
  display: flex;
}

.navigation__list:first-child {
  margin-right: 32px;
}

.navigation__link {
  text-transform: uppercase;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  transition: transform .5s ease-in-out;
}

.navigation__link:hover {
  transform: scale(1.3);
}

.logo {
  height: 24px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.logo__icon {
  stroke: var(--color-text);
  width: 24px;
  height: 100%;
  box-shadow: 0;
  transition: box-shadow .5s ease-in-out, border-radius .5s ease-in-out;
  animation-name: logoRotate;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.logo__icon:hover, .logo__icon:focus {
  box-shadow: 1px 1px 10px 5px var(--color-text);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.logo__title {
  color: var(--color-text);
  transition: scale var(--default-transition);
  border-radius: 10px;
  margin-left: 34px;
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 35px;
  display: none;
}

.logo__title:hover, .logo__title:focus {
  scale: 1.05;
}

@media screen and (min-width: 768px) {
  .logo__title {
    justify-content: center;
    align-items: center;
    display: flex;
    overflow: hidden;
  }
}

.logo__title span {
  transition: all 1s;
}

.logo__title span:nth-child(10n+1) {
  transition-delay: .1s;
}

.logo__title span:nth-child(10n+2) {
  transition-delay: .25s;
}

.logo__title span:nth-child(10n+3) {
  transition-delay: .4s;
}

.logo__title span:nth-child(10n+4) {
  transition-delay: .55s;
}

.logo__title span:nth-child(10n+5) {
  transition-delay: .7s;
}

.logo__title span:nth-child(10n+6) {
  transition-delay: .85s;
}

.logo__title span:nth-child(10n+7) {
  transition-delay: 1s;
}

.logo__title span:nth-child(10n+8) {
  transition-delay: 1.15s;
}

.logo__title span:nth-child(10n+9) {
  transition-delay: 1.3s;
}

.logo__title:hover span {
  filter: blur(20px);
  opacity: 0;
  transform: scale(2);
}

@keyframes logoRotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.form-search {
  position: relative;
}

.form-search__input {
  border: none;
  border-bottom: 1px solid var(--color-text);
  color: var(--color-text);
  width: 280px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  padding: 2px 23px 2px 2px;
  font-size: 14px;
  transition: border-radius .4s linear, border-bottom .5s linear, box-shadow .4s linear;
}

.form-search__input::-ms-input-placeholder {
  color: var(--color-text);
}

.form-search__input::placeholder {
  color: var(--color-text);
}

.form-search__input:hover, .form-search__input:focus {
  box-shadow: 1px 2px 25px 0px var(--color-text);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .form-search__input {
    width: 336px;
  }
}

@media screen and (min-width: 1200px) {
  .form-search__input {
    width: 394px;
  }
}

.form-search__submit {
  height: 100%;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

.form-search__icon {
  stroke: var(--color-text);
  width: 12px;
  height: 12px;
}

.auth-button {
  color: var(--color-text);
  border: 1px solid var(--bgc-footer-arrow);
  transition: background-color 500ms var(--cubic-bezier);
  background-color: rgba(0, 0, 0, 0);
